<template>
    <div>
        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;margin-top:15px;">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.name" placeholder="编号"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="getUsers">查询</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="addUser">新增单车</el-button>
                </el-form-item>
                <router-link to="/admin/import">
                    <el-button type="primary">导入单车</el-button>
                </router-link>
                <!-- <el-button type="primary" @click="exportWord">导出<i class="el-icon-upload el-icon--right"></i></el-button> -->
            </el-form>
        </el-col>


        <el-table :data="tableData()" style="width: 100%" >
            <el-table-column prop="bicycleNo" label="编号" width="90">
            </el-table-column>
            <el-table-column prop="color" label="颜色" width="180">
            </el-table-column>
            <el-table-column prop="statusName" label="是否租赁" width="180">
            </el-table-column>
            <el-table-column prop="createTime" label="创建时间" width="180">
            </el-table-column>
            <el-table-column prop="updateTime" label="修改时间" width="180">
            </el-table-column>
            <el-table-column prop="addUser.name" label="添加人" width="100">
            </el-table-column>
            <!--第二步  开始进行修改和查询操作-->
            <el-table-column label="操作" align="center" min-width="100">

                <template slot-scope="scope">
                    <el-button type="danger" @click="deleteUser(scope.row)">报废</el-button>
                </template>
            </el-table-column>
        </el-table>
         <!--新增界面-->
        <el-dialog title="记录" :visible.sync="dialogVisible" width="500px" :close-on-click-modal="false" :destroy-on-close="true">
            <el-form :model="bicycle" :rules="rules2" ref="bicycle" label-width="0px" class="demo-ruleForm">
                <el-form-item prop="bicycleNo" :error="isExists">
                    <el-input type="text" v-model="bicycle.bicycleNo" auto-complete="off" placeholder="编号" @blur="isExist"></el-input>
                </el-form-item>
                <el-form-item prop="color">
                    <el-input type="text" v-model="bicycle.color" auto-complete="off" placeholder="颜色"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click.native="dialogVisible = false,bicycle={bicycleNo:'',color:''}">取 消</el-button>
                <el-button v-if="isView" type="primary" @click.native="addSubmit">确 定</el-button>
            </span>
        </el-dialog>
    </div>

</template>

<script>
    import axios from 'axios';
    import qs from 'qs';
    export default {
        name: 'home',
        inject:['reload'],
        data() {
            return {
                userInfoList: [],
                dialogVisible:false,
                isView: true,
                bicycle: {
                    bicycleNo: '',
                    color:''
                },
                rules2: {
                    bicycleNo: [{
                        required: true,
                        message: '编号不能为空',
                        trigger: 'blur'
                    }],
                    color: [{
                        required: true,
                        message: '颜色不能为空',
                        trigger: 'blur'
                    }]
                },
                filters: {
                    name: ''
                },
                token:'',
                isExists:'',
                user:{}
            }
        },

        methods: {
             isExist(){
                 this.isExists="";
                if(this.bicycle.bicycleNo){
                    this.axios.get("/bicycle/isExist?no="+this.bicycle.bicycleNo).then(res=>{
                        console.log(res);
                        
                        if(res.data.data){
                            this.isExists = "该单位编码已存在"
                        }
                        console.log(this.isExists);
                    })
                }
            },
            //  exportWord() {
            //     this.$exportExcel({
            //         url: "/user/exportExcel",
            //         data: {},
            //         responseType: "blob",
            //         fileName: "用户记录",
            //         method: 'post'
            //     });
            // },
            loadData() {
                // let param = {filter:this.filters.name};
                this.axios.get(this.$baseUrl()+'/bicycle/list').then((res) => {
                    console.log(res);                    
                    this.userInfoList = res.data.data;
                });

            },
             tableData() {
                let status = {
                    0: "未租用",
                    1: "已租用"
                };
                return this.userInfoList.map(it => {
                    it.statusName = status[it.isRent];
                    return it;
                });
            },
            getUsers() {
                this.axios.get("bicycle/findByNoLike",{
                    params:{
                        bicycleNo:this.filters.name
                    }
                }).then(res=>{
                    console.log("res:",res);
                    this.userInfoList = res.data.data;
                })
            },
            addUser(){
                this.bicycle = {
                    bicycleNo: '',
                    color: ''
                };
                this.isExists="";
                this.isView = true;
                this.dialogVisible = true;
            },
            deleteUser(row) {
                console.log("id: ",row.id);
                this.axios.post("bicycle/scrap",this.$qs.stringify({id:row.id})).then(res=>{
                    console.log("res: ",res);
                    if(res.data.code===200){
                        this.$message({
                            message:"删除成功",
                            type:'success'
                        })
                        this.reload();
                    }else{
                            this.$message({
                                message: res.data.message,
                                type:'error'
                            });
                            
                        this.reload();
                    }
                })
            },
            addSubmit() {

                //先判断表单是否通过了判断
                this.$refs.bicycle.validate((valid) => {
                    //代表通过验证 ,将参数传回后台
                    if (valid) {
                        console.log(this.bicycle);
                        let param = {
                            bicycle:this.bicycle,
                            userId:this.user.id
                        }
                        this.axios.post("bicycle/add",this.$qs.stringify({
                            bicycleNo:this.bicycle.bicycleNo,
                            color:this.bicycle.color,
                            userId:this.user.id
                        })).then(res => {
                            console.log(res);
                            
                            if (res.data.code===200) {
                                    this.$message({
                                            type: 'success',
                                            message: '添加成功',
                                        }); 
                                this.loadData();
                            } else {
                                this.$message({
                                    type: 'error',
                                    message: res.data.message
                                });
                            }
                            this.dialogVisible = false;
                        });
                    }

                });
            }
        },
        created(){
            this.user = this.$store.state.user.accessToken;
            console.log("user: ",this.user);
            
            this.loadData();
        }
    }
</script>

<style>
body {
        background: #DFE9FB;
    }
.demo-ruleForm{
  margin-left: 10%;  
  width: 80%;
}
.el-table .success-row {
    background: #f0f9eb;
  }
</style>


